<template lang="pug">
    div(class="page")
        Header
        section(class="single-blog")
            div(class="container")
                div(class="single-blog-content")
                    div(class="single-blog-box wow fadeInUp" data-wow-duration="1s")
                        div(class="post-image")
                            img(:src="blog.image", alt="")
                        div(class="post-text")
                            h2.post-title {{ blog.title }}
                            .post-details(v-html="blog.body")
        Footer
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";

import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

Vue.use(VueAxios, axios);

export default {
    name: "SingleBlog",
    components: {
        Header,
        Footer,
    },
    data() {
        return {
            url: this.$route.params.url,
            blog: [],
        };
    },
    mounted() {
        this.axios.get("blogs/" + this.url).then((singleBlogApi) => {
            // console.log(singleBlogApi.data.data.blogs[0]);
            this.blog = singleBlogApi.data.data.blogs[0];
        });
    },
};
</script>
